import styled from 'react-emotion'
import theme from '../theme'
import {
  space,
  width,
  height,
  display,
  color,
  borders,
  borderColor,
  borderRadius,
} from 'styled-system'

const Progress = styled.progress(
  space,
  width,
  height,
  display,
  color,
  borders,
  borderColor,
  borderRadius,
  {
    overflow: 'hidden',
    webkitAppearance: 'none',
    appearance: 'none',
    '&[value]::-webkit-progress-bar': {
      backgroundColor: this.props.barBG,
    },
    '&[value]::-webkit-progress-value': {
      backgroundColor: this.props.valueBG,
    },
    verticalAlign: 'top',
  }, {...props}
)

Progress.defaultProps = {
  width: 1,
  height: 8,
  max: 100,
  value: 64,
  barBG: 'theme.colors.gray[4]',
  valueBG: 'theme.colors.blue[4]',
}

export default Progress
